#landing .newLine {
  display: block;
  word-wrap: break-word;
}
#aboutMe .newLine {
  display: block;
  word-wrap: break-word;
}
#aboutMe .bold {
  font-weight: bold;
  font-size: 30px;
}
#landing .social-links {
  display: flex;
  justify-content: space-between;
  width: 30vw;
  margin-top: 5%;
}
#landing .social-links i {
  font-size: 5em;
  color: rgba(214, 202, 202, 0.808);
}
#landing .social-links i:hover {
  color: rgb(255, 255, 255);
}
#aboutMe {
  background: #d3cce3; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #e9e4f0,
    #d3cce3
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #e9e4f0,
    #d3cce3
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  color: black;
  display: flex;
  align-items: center;
  font-family: "Raleway", sans-serif;
  padding: 2.5% 0 1% 0;
}
#aboutMe .left {
  margin-left: 5%;
  width: 35%;
}
#aboutMe .right {
  margin-right: 5%;
  width: 50%;
  margin-left: 5%;
  font-size: 1.5rem;
}
#aboutMe .left .title {
  margin-top: 15px;
  font-weight: bold;
  font-family: "Orbitron", sans-serif;
  font-size: 3rem;
  text-decoration: underline;
  padding: 1.75% 0;
}
#aboutMe .right .title {
  margin-bottom: 15px;
  font-size: 1.5rem;
}
#aboutMe .left h2 {
  font-family: "Raleway", sans-serif;
  font-style: italic;
  font-size: 1.75rem;
  line-height: 3rem;
}
#aboutMe .right .languages {
  display: flex;
  justify-content: space-evenly;
  font-weight: bolder;
  flex-wrap: wrap;
  margin: 5% 0;
  padding: 2.5% 1%;
  background-color: #575757;
  filter: opacity(80%);
  color: rgb(255, 255, 255);
  border: none;
  padding: 2.5% 1%;
  font-size: 1rem;
}
#aboutMe p {
  font-size: 1.5rem;
  line-height: 2.5rem;
}
#resume {
  background: #d3cce3; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #e9e4f0,
    #d3cce3
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #e9e4f0,
    #d3cce3
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  color: black;
  font-family: "Raleway", sans-serif;
  padding: 2.5% 0 0.25% 0;
}
#resume .button {
  text-decoration: none;
  margin: 1.25% auto 2.5% auto;
  padding: 1.25% 1.75%;
  width: 20%;
  background: #141e30; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #243b55,
    #141e30
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #243b55,
    #141e30
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  transition: all 0.5s;
  border-radius: 10px;
  border: none;
}
#resume .button a {
  text-decoration: none;
  color: rgb(230, 220, 220);
  font-style: unset;
}
#resume .button span {
  cursor: pointer;
  display: inline-block;
  position: relative;
  transition: 0.5s;
  font-style: normal;
  font-size: 1.5em;
}
#resume .button span:after {
  content: "\00bb";
  position: absolute;
  opacity: 0;
  top: 0;
  right: -20px;
  transition: 0.5s;
}
#resume .button:hover span {
  padding-right: 25px;
}
#resume .button:hover span:after {
  opacity: 1;
  right: 0;
}
#resume .wrapper {
  background-color: #17171b;
  margin: 5%;
  color: whitesmoke;
}
#resume #education,
#resume #experience,
#resume #awards {
  margin: 0 5%;
  padding: 1.25% 0 2.5% 0;
  width: 90%;
  border-bottom: 2.5px solid #b42929;
  text-align: left;
}
#resume #awards {
  border-bottom: none;
}
#resume .section-title {
  font-size: 3rem;
  font-family: "Orbitron", sans-serif;
}
#resume .title {
  font-size: 2.5rem;
  margin: 1.25% 0 0 0;
  line-height: 35px;
  color: #4e517e;
}
#resume .subtitle {
  font-size: 1.25rem;
  font-style: italic;
  line-height: 25px;
  margin: 0;
  color: #c1edff;
}
#resume .bullets {
  padding-left: 2.5%;
  font-size: 1.25rem;
  color: #e6e6e6;
}
#projects {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  align-items: center;
  padding: 1.5% 0 5% 0;
  color: black;
  background: #d3cce3; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #e9e4f0,
    #d3cce3
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #e9e4f0,
    #d3cce3
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
#projects .project {
  max-width: 30%;
  border: none;
  margin: 1.5%;
}
#projects .project img {
  max-height: 200px;
  max-width: 95%;
  margin: 2.5% auto 0 auto;
  filter: drop-shadow(5px 10px 2px #4b4747);
}
#projects .description {
  width: 90%;
  padding: 0 5% 2.5% 5%;
}
#projects .description .languages {
  font-size: 1rem;
  margin-top: 20px;
  font-family: "Orbitron", sans-serif;
  display: flex;
  justify-content: space-around;
  width: 100%;
}
#projects .description .title {
  font-size: 2rem;
  font-family: "Orbitron", sans-serif;
}
#projects .description p {
  font-size: 1.25rem;
  font-family: "Raleway", sans-serif;
}
#projects .description .button {
  text-decoration: none;
  margin: auto;
  padding: 2.5% 1.75%;
  width: 40%;
  background: #141e30; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #243b55,
    #141e30
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #243b55,
    #141e30
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  transition: all 0.5s;
  border-radius: 10px;
  border: none;
}
#projects .description.button a {
  text-decoration: none;
  font-style: unset;
  font-family: "Orbitron", sans-serif;
  width: 100%;
  height: 100%;
}
#projects .description .button span {
  cursor: pointer;
  display: inline-block;
  position: relative;
  transition: 0.5s;
  font-style: normal;
  font-size: 1.5em;
  color: rgb(230, 220, 220);
  height: 100%;
}
#projects .description .button span:after {
  content: "\00bb";
  position: absolute;
  opacity: 0;
  top: 0;
  right: -20px;
  transition: 0.5s;
}
#projects .description .button:hover span {
  padding-right: 25px;
}
#projects .description .button:hover span:after {
  opacity: 1;
  right: 0;
}
#projects .java,
#projects .javaFx {
  color: red;
}
#projects .python,
#projects .tkinter {
  color: rgb(0, 33, 105);
}
#projects .html5 {
  color: orangered;
}
#projects .css3 {
  color: rgb(0, 132, 255);
}
#projects .jQuery {
  color: rgb(5, 4, 90);
}
#projects .react,
#projects .typescript,
#projects .sqlite {
  color: rgb(4, 112, 139);
}
#projects .unity {
  color: rgb(30, 30, 36);
}
#projects .firebase {
  color: orange;
}
#projects .languages {
  color: rgb(73, 9, 133);
}
.content {
  color: white;
  height: auto;
  width: 100%;
  text-align: center;
  background: #b92b27; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #1565c0,
    #b92b27
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #1565c0,
    #b92b27
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.content h1 {
  margin: 0;
}
.link::after {
  content: "";
  display: block;
  position: relative;
  top: -17.5px;
  width: 0;
  height: 2px;
  background-color: white;
  transition: width 0.3s;
}

.link:hover::after {
  width: 100%;
  transition: width 0.3s;
}

div.mdl-layout__drawer-button {
  display: none;
}
@media screen and (max-width: 1024px) {
  #hamMenu {
    display: block;
  }
  div.mdl-layout__drawer-button {
    color: white;
  }
  span.mdl-layout__title,
  span.mdl-layout-title {
    font-size: 15px;
  }
  #landing h1 {
    font-size: 10vw;
  }
  #landing .social-links {
    width: 30vw;
  }
  #landing .social-links i {
    font-size: 4.5em;
  }
  #aboutMe .left {
    margin-left: 2.5%;
    width: 37.5%;
  }
  #aboutMe .right {
    margin-right: 2%;
    margin-left: 1.25%;
    width: 56.5%;
    font-size: 1.25rem;
  }
  #aboutMe .left .title {
    font-size: 2.5rem;
    padding: 1.25% 0;
  }
  #aboutMe .right .title {
    font-size: 1.125rem;
  }
  #aboutMe .left h2 {
    font-size: 1.0625rem;
    line-height: 2.5rem;
  }
  #aboutMe .bold {
    font-size: 1.5rem;
  }
  #aboutMe .right .languages {
    margin: 5% 0;
    padding: 1.25% 0.75%;
    font-size: 0.875rem;
  }
  #aboutMe p {
    font-size: 1.125rem;
    line-height: 2rem;
  }
  #resume .button {
    margin: 3% auto 5% auto;
    width: 30%;
  }
  #resume .wrapper {
    margin: 5% 2.5%;
  }
  #resume .section-title {
    font-size: 2.5rem;
  }
  #resume .title {
    font-size: 2.25rem;
    margin: 1.125% 0 0 0;
    line-height: 2rem;
  }
  #resume .subtitle {
    font-size: 1.25rem;
    line-height: 2rem;
    margin: 0;
  }
  #resume .bullets {
    padding-left: 2.5%;
    font-size: 1rem;
  }
  #projects .project {
    max-width: 45%;
    margin: 2.5%;
  }
  #projects .description .title {
    font-size: 1.75rem;
  }
  #projects .description p {
    font-size: 1.125rem;
  }
}

@media screen and (max-width: 780px) {
  #hamMenu {
    position: sticky;
  }
  .mdl-layout__header-row .mdl-navigation {
    display: none;
  }
  .mdl-layout__header-row > * {
    width: 100%;
    text-align: right;
  }
  span.mdl-layout__title,
  span.mdl-layout-title {
    font-size: 1.125rem;
  }
  div.mdl-layout__drawer-button {
    display: block;
  }
  #landing .social-links {
    width: 30vw;
  }
  #landing .social-links i {
    font-size: 3em;
  }
  #aboutMe {
    font-weight: 300;
    display: block;
    padding: 2.5% 0 2.5% 0;
    text-align: center;
  }
  #aboutMe .left {
    margin: 0 auto;
    width: 90%;
  }
  #aboutMe .right {
    margin: 0 auto;
    width: 90%;
    font-size: 1.5rem;
    line-height: 2.5rem;
  }
  #aboutMe .left .title {
    font-size: 4rem;
  }
  #aboutMe .right .title {
    font-size: 1.5rem;
    line-height: 2.5rem;
  }
  #aboutMe .left h2 {
    font-size: 1.5rem;
    line-height: 2.5rem;
  }
  #aboutMe .bold {
    font-size: 2rem;
  }
  #aboutMe .right .languages {
    margin: 5% auto;
    padding: 2.5% 1.5%;
    font-size: 1.125rem;
    width: 95%;
  }
  #aboutMe p {
    font-size: 1.5rem;
    line-height: 2.5rem;
    width: 100%;
  }
  #resume .button {
    margin: 3% auto 5% auto;
    padding: 2.5% 1.25%;
    width: 40%;
  }
  #resume .section-title {
    font-size: 2.25rem;
  }
  #resume .title {
    font-size: 1.7rem;
    line-height: 1.5rem;
  }
  #resume .subtitle {
    font-size: 1.125rem;
    line-height: 1.75rem;
  }
  #resume .bullets {
    padding-left: 5%;
    font-size: 0.875rem;
  }
}
@media screen and (max-width: 600px) {
  #aboutMe .left .title {
    font-size: 3.5rem;
  }
  #aboutMe .right .title {
    font-size: 1.0625rem;
    line-height: 2rem;
  }
  #aboutMe .left h2 {
    font-size: 1.0625rem;
    line-height: 2rem;
  }
  #aboutMe .bold {
    font-size: 1.5rem;
  }
  #aboutMe .right .languages {
    margin: 5% auto;
    padding: 2% 1%;
    font-size: 0.875rem;
    width: 98%;
  }
  #aboutMe p {
    font-size: 1.0625rem;
    line-height: 2rem;
    width: 100%;
    margin-bottom: 0;
  }
  #resume .button {
    margin: 1% auto 5% auto;
    padding: 2.5% 1.25%;
    width: 40%;
  }
  #resume .button span {
    font-size: 1.125rem;
  }
  #resume .section-title {
    font-size: 2rem;
  }
  #resume .title {
    font-size: 1.5rem;
  }
  #resume .subtitle {
    font-size: 1rem;
    line-height: 1.5rem;
  }
  #resume .bullets {
    padding-left: 5%;
    font-size: 0.875rem;
    line-height: 1.5rem;
  }
  #projects .description .title {
    font-size: 1.5rem;
  }
  #projects .description p {
    font-size: 1rem;
  }
}
@media screen and (max-width: 480px) {
  span.mdl-layout__title,
  span.mdl-layout-title {
    font-size: 0.875rem;
  }
  div.mdl-layout__drawer {
    width: 240px;
  }
  #landing h1 {
    font-size: 50px;
  }
  #landing .social-links {
    width: 50vw;
  }
  #landing .social-links i {
    font-size: 50px;
  }
  #aboutMe .left .title {
    font-size: 3rem;
  }
  #aboutMe .right .title {
    font-size: 1rem;
    line-height: 2rem;
  }
  #aboutMe .left h2 {
    font-size: 1.25rem;
    line-height: 2rem;
  }
  #aboutMe .bold {
    font-size: 1.5rem;
    font-weight: bold;
  }
  #aboutMe .right .languages {
    margin: 5% auto;
    padding: 1% 0.5%;
    font-size: 0.75rem;
    width: 98%;
  }
  #aboutMe p {
    font-size: 1rem;
    line-height: 1.85rem;
    width: 100%;
    margin-bottom: 0;
  }
  #resume .button {
    margin: 1% auto 5% auto;
    padding: 3% 1.25%;
    width: 60%;
  }
  #resume .button span {
    font-size: 1.125rem;
  }
  #resume .section-title {
    font-size: 1.75rem;
  }
  #resume .title {
    font-size: 1.25rem;
  }
  #resume .subtitle {
    font-size: 0.875rem;
    line-height: 1.25rem;
  }
  #resume .bullets {
    font-size: 0.8rem;
    line-height: 1.25rem;
  }
  #projects .project {
    max-width: 75%;
    margin-top: 7.5%;
  }
}
@media screen and (max-width: 300px) {
  span.mdl-layout__title,
  span.mdl-layout-title {
    font-size: 0.625rem;
  }
  div.mdl-layout__drawer {
    width: 160px;
  }
  #landing h1 {
    font-size: 35px;
  }
  #landing .social-links {
    width: 50vw;
  }
  #landing .social-links i {
    font-size: 30px;
  }
  #aboutMe .left .title {
    font-size: 2.5rem;
  }
  #aboutMe .right .title {
    font-size: 0.9rem;
    line-height: 1.75rem;
  }
  #aboutMe .left h2 {
    font-size: 1.125rem;
    line-height: 1.75rem;
  }
  #aboutMe .bold {
    font-size: 1.25rem;
    font-weight: bold;
  }
  #aboutMe .right .languages {
    margin: 5% auto;
    padding: 1% 0.5%;
    font-size: 0.625rem;
    width: 98%;
  }
  #aboutMe p {
    font-size: 1rem;
    line-height: 1.85rem;
    width: 100%;
    margin-bottom: 0;
  }
  #resume .button {
    margin: 1% auto 5% auto;
    padding: 3% 1.25%;
    width: 60%;
  }
  #resume .button span {
    font-size: 1.125rem;
  }
}
